<template>
    <div class="container">
        <TodoItem
            :todos="incompleteTodos"
            :setCurrentTodo="setCurrentTodo"
            :toggleTodoCompletion="toggleTodoCompletion"
        />
        <button class="btn btn-outline-secondary my-3" @click="toggleShowCompleted($event)">
            <i v-if="showCompleted" class="bi bi-chevron-down"></i>
            <i v-else class="bi bi-chevron-right"></i>
             Completed {{ completedCount }}
        </button>
        <Transition>
            <TodoItem
                v-show="showCompleted"
                :todos="completedTodos"
                :setCurrentTodo="setCurrentTodo"
                :toggleTodoCompletion="toggleTodoCompletion"
            />
        </Transition>
    </div>
    <TodoModal
        v-if="currentTodo !== null"
        modalId="editTodoModal"
        :isNew="false"
        :todo="currentTodo"
        :deleteTodo="deleteTodo"
        :toggleTodoCompletion="toggleTodoCompletion"
    />
    <TodoModal
        modalId="newTodoModal"
        :isNew="true"
        :todo="currentTodo"
        :addNewTodo="addNewTodo"
    />
    <div class="fixed-bottom" style="height: 70px;">
        <div class="container" style="height: 50px;">
            <div class="d-flex justify-content-end">
            <datalist id="itemOptions">
                <option v-for="(name, index) in uniqueItems" :key="index" :value="name" >
                </option>
            </datalist>
            <button style="height: 50px;width: 50px;" data-bs-toggle="modal" data-bs-target="#newTodoModal" class="btn btn-primary rounded-circle fs-5 shadow" type="button"><i class="bi bi-plus-lg"></i></button>
            </div>
        </div>
    </div>
</template>

<script>
import TodoItem from './TodoItem.vue';
import TodoModal from './TodoModal.vue';
const blankTodoObject = {name:"",done:false,id:0};

export default {
    data() {
        return {
            newTodo: '',
            todos: JSON.parse(localStorage.getItem('todoItems')) || [],
            showCompleted: false,
            currentTodo: blankTodoObject
        };
    },
    components: {
        TodoItem,
        TodoModal
    },
    computed: {
        sortedTodos() {
            return [...this.todos].sort((a,b)=>b.id - a.id);
        },
        completedTodos() {
          return this.sortedTodos.filter(todo => todo.done);
        },
        incompleteTodos() {
          return this.sortedTodos.filter(todo => !todo.done);
        },
        completedCount() {
          return this.completedTodos.length;
        },
        uniqueItems(){
            return [...new Set(this.todos.map(todo => todo.name))].sort()
        }
      },
    methods: {
        capitalizeFirstLetter(string) {
            string = string.trim();
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        removeFocus(event) {
            setTimeout(()=>{
              event.target.blur();
            },250)
        },
        toggleShowCompleted(event){
            this.showCompleted = !this.showCompleted;
            this.removeFocus(event);
        },
        addNewTodo(todo){
            const tempTodo = {...todo};
            tempTodo.name = this.capitalizeFirstLetter(tempTodo.name);
            tempTodo.id = Date.now();
            if (tempTodo.name){
                this.todos.push(tempTodo);
                this.saveTodos();
            }
            this.currentTodo = {...blankTodoObject, id:Date.now()};
        },
        addTodo() {
            if (this.newTodo.trim()) {
                this.todos.push({ id: Date.now(), name: this.capitalizeFirstLetter(this.newTodo), done: false });
                this.newTodo = '';
                this.saveTodos();
            }
        },
        toggleTodoCompletion(todo) {
          // Temporarily mark the todo as not completed
          const todoDone = todo.done;
          todo.done = todoDone;
          this.saveTodos();
        },
        setCurrentTodo(todo){
            this.currentTodo = todo;
        },
        deleteTodo(id) {
            this.todos = this.todos.filter(todo => todo.id !== id );
            this.saveTodos();
        },
        saveTodos() {
            localStorage.setItem('todoItems', JSON.stringify(this.todos));
        }
    }
};
</script>

<style>
.btn {
  z-index: 1;
}
.fixed-bottom {
  opacity: .95;
}
.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>
